/* eslint-disable no-unused-vars */
const registerChatRoutes = (server) => {
    const serverConfig = server;

    serverConfig.namespace = '/v1/api/gbr/';
    const mockGetConfig = (schema, request) => {
        return {
            hoop: '9AM-6PM',
            isAgentOnline: false,
            ewt: 10,
            isLeaveAgentMessageEnabled: false,
            leaveAgentMessage:
                'Please leave a message and we will get back to you as soon as we can.',
            userInactiveInterval: 10,
            isSuccess: true,
            errors: undefined,
        };
    };
    serverConfig.get('config', mockGetConfig);
    const mockLeaveMessage = (schema, request) => {
        return {
            isSuccess: false,
            errors: undefined,
        };
    };
    serverConfig.post('leaveagentmessage', mockLeaveMessage);
};

export default registerChatRoutes;
